import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  getUserdata,
  localStoragedecryp,
  pembulatan,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportBatalPenjualan = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_baraang = 0;
  let total_qty = 0;
  let total_harga_gram = 0;
  let total_harga_beli = 0;
  let total_berat = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN BARANG TUKAR", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN BARANG TUKAR",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  tableColumn = [
    [
      {
        content: `NAMA BARANG`,
      },
      {
        content: `QTY`,
        styles: {
          halign: 'right'
        },
      },
      {
        content: `HARGA/GRAM`,
        styles: {
          halign: 'right'
        },
      },
      {
        content: `BERAT`,
        styles: {
          halign: 'right'
        },
      },
      
      {
        content: `TOTAL HARGA`,
        styles: {
          halign: 'right'
        },
      },
    ],
  ];
  data.forEach((item, index) => {
    let tot_qty = 0;
    let tot_berat = 0;
    let tot_harga_beli = 0;
    let tot_harga_gram = 0;
    let header = [
      {
        content: `Nomor Faktur Group : ${
          item.no_faktur_group_user
        }`,
        colSpan: 5,
      },
    ];
    tableRows.push(header);
    item.detail_barang.forEach((item, index) => {
      let rows = [
        item.nama_barang,
        {
          content: parseInt(item.qty),
          styles: {
            halign: 'right',
          },
        },
        {
          content:pembulatan(parseInt(item.harga_gram)).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
          },
        },
        {
          content: item.berat.toFixed(2),
          styles: {
            halign: 'right',
          },
        },
        
        {
          content:  parseInt(item.harga_beli).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
          },
        },

      ];
      total_qty = parseFloat(total_qty) + parseFloat(item.qty);
      total_harga_gram = total_harga_gram + parseInt(item.harga_gram);
      total_berat = total_berat + parseFloat(item.berat);
      total_harga_beli = total_harga_beli + parseInt(item.harga_beli);

      tot_qty = tot_qty + parseFloat(item.qty);
      tot_harga_gram = tot_harga_gram + parseInt(item.harga_gram);
      tot_berat = tot_berat + parseFloat(item.berat);
      tot_harga_beli = tot_harga_beli + parseInt(item.harga_beli);

      tableRows.push(rows);
    });

    total_baraang = total_baraang + item.detail_barang.length;
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    // let total = [
    //   {
    //     content: `Sub Total : ${item.detail_barang.length}`,
    //     colSpan: 1,
    //     styles: {
    //       halign: 'center',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${tot_qty}`,
    //     styles: {
    //       halign: 'right',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${parseInt(tot_harga_gram).toLocaleString("kr-KO")}`,
    //     styles: {
    //       halign: 'right',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${formatGram(tot_berat,3)}`,
    //     styles: {
    //       halign: 'right',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
     
    //   {
    //     content: `${parseInt(tot_harga_beli).toLocaleString("kr-KO")}`,
    //     styles: {
    //       halign: 'right',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    // ];
    // tableRows.push(total);

  });
  let footer = [
    {
      content: `Grand Total Barang : ${total_baraang}`,
      colSpan: 1,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_qty}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${pembulatan(parseInt(total_harga_gram)).toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat.toFixed(2)}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
   
    {
      content: `${parseInt(total_harga_beli).toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(footer);

  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 11,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  footRows = [];
  // total_baraang = 0;
  // tot_berat = 0;
  // tot_onkos = 0;
  // tot_harga_jual = 0;
  // tot_harga_gram = 0;
  // tot_berat_atribut = 0;
  // tot_harga_atribut = 0;
  // tot_harga_total = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(
  //   `User                           :  ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  // doc.text(`Total Barang Yang Di batal : ${total_baraang}`, 14, finalY + 20);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN BATAL PENJUALAN.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN BARANG TUKAR</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportBatalPenjualan;
