import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem, pembulatan } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import ExportBatalPenjualan from "./pdf/ExportPenjualanTukar";
import Loading from 'react-fullscreen-loading';
import LaporanExelPenjualanTukar from "./exel/LaporanExelPenjualanTukar";

class HeadFormLaporanPenjualanTukar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    ExportBatalPenjualan(this.props.LaporanDataPenjualan);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let databarang = [];
    let qty = 0;
    let kadar = 0;
    let berat = 0;
    let harga_nota = 0;
    let harga_beli = 0;
    let harga_gram = 0;
    this.props.LaporanDataPenjualan.forEach((element) => {
      element.detail_barang.forEach((detail) => {
        databarang.push(detail);
        qty = qty + detail.qty
        kadar = kadar + detail.kadar
        berat = berat + detail.berat
        harga_nota = harga_nota + detail.harga_nota
        harga_beli = harga_beli + detail.harga_beli
        harga_gram = harga_gram + detail.harga_gram
      });
    });
    const columns = [
      // {
      //   dataField: "kode_dept",
      //   text: "Jenis",
      //   footer: '',
      // },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
        footerAttrs: {
          colSpan: "1",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang  : {databarang.length} </div>;
        },
      },
     
      {
        dataField: "qty",
        text: "Qty",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{qty} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell}</div>;
        },
      },

      {
        dataField: "harga_gram",
        text: "Harga/Gram",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{pembulatan(harga_gram).toLocaleString("kr-KO")} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{pembulatan(cell).toLocaleString("kr-KO")}</div>;
        },
      },
     
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{berat.toFixed(2)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toFixed(2)}</div>;
        },
      },
      {
        dataField: "harga_beli",
        text: "Total Harga",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{harga_beli.toLocaleString("kr-KO")} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString("kr-KO")}</div>;
        },
      }
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang  :  []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Batal Penjualan Kosong"
            />
          </div>
          
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanExelPenjualanTukar data={this.props.LaporanDataPenjualan} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormLaporanPenjualanTukar = reduxForm({
  form: "HeadFormLaporanPenjualanTukar",
    enableReinitialize: true,
  validate: ValidasiTanggalLaporan 
})(HeadFormLaporanPenjualanTukar);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPenjualanTukar);


